.carousel-section {
  margin-top: 120px;
  color: black;
  padding: 20px;
  position: relative;
}

.carousel-section h2 {
  font-size: 18px !important;
  position: absolute !important;
  top: -30px !important;
  left: 22% !important;
  color: #d47f8a;
}

.carousel-container {
  display: flex;
  align-items: center;
  position: relative;
  justify-content: center;
  padding: 0;
  overflow-x: hidden; /* 기본적으로 숨김 */
}

.carousel-container.expanded {
  overflow: visible; /* 확장된 콘텐츠가 넘칠 수 있도록 설정 */
}

.carousel-content {
  display: flex;
  scroll-behavior: smooth;
  width: 60%;
  position: relative;
}

.carousel-item {
  min-width: 200px;
  height: 150px;
  margin-right: 10px;
  background-color: #333;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  color: white;
  position: relative;
  transition: all 0.3s ease; /* 부드러운 전환 효과 */
  transform: translateX(0); /* 확장 전 기본 위치 유지 */
}

.carousel-item.expanded {
  z-index: 10; /* 확장된 항목이 다른 요소 위로 올라오도록 설정 */
  min-width: 500px; /* 확장된 너비 */
  height: 450px; /* 확장된 높이 */
  background-color: #555;
  flex-direction: column; /* 내용 정렬 */
  transform: scale(1.5); /* 확장 시 크기 증가 */
  transition: all 0.3s ease; /* 부드러운 확장 애니메이션 */
}

.expanded-content {
  margin-top: 10px;
  font-size: 16px;
  color: #ffd1dc;
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #333;
  padding: 10px;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* 확장된 콘텐츠 그림자 */
  z-index: 20;
}

.carousel-button {
  background-color: white;
  border: none;
  color: #ffd1dc;
  font-size: 48px;
  cursor: pointer;
  width: 40px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
  opacity: 0;
  transition: opacity 0.3s ease, background-color 0.3s ease;
}

.carousel-container:hover .carousel-button {
  opacity: 1;
}

.carousel-button:focus,
.carousel-button:active {
  background-color: white;
  color: #ffd1dc;
  outline: none;
  border: none;
}

.carousel-button:hover {
  background-color: white !important;
  color: #ffd1dc;
}

.prev {
  margin-right: 5px;
}

.next {
  margin-left: 5px;
}
